import React from "react";
import { DisplayBead } from "../../Bead/DisplayBead";
import { buildBead } from "../../../editorObjects/Bead";
import { buildPoint } from "../../../editorObjects/Point";

export const FieldBeadPreview = ({ bead }) => {
  const displayBead = buildBead({
    ...bead,
    position: buildPoint(0, 0),
  });
  return (
    <div
      style={{
        marginTop: "20px",
        marginBottom: "20px",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        height: "100px",
        boxSizing: "border-box",
      }}
    >
      <div
        style={{
          position: "relative",
          top: `${(-1 * bead.height) / 2}px`,
          left: `${(-1 * bead.width) / 2}px`,
        }}
      >
        <DisplayBead
          bead={displayBead}
          isSelected={false}
          isSelectable={false}
        />
      </div>
    </div>
  );
};
