import React from "react";
import { Bead, BeadTypes } from "../../editorObjects/Editor";
import { getTranslate3dCss } from "../operations/MapValuesToCss";
import { Actions } from "../../store";
import { useDispatch } from "react-redux";

export const DisplayBead = (props: {
  bead: Bead;
  opacity?: number;
  zIndex?: number;
  isSelected: boolean;
  isSelectable: boolean;
}) => {
  const { bead, opacity, zIndex, isSelected, isSelectable } = props;
  const isOpacity = opacity !== undefined && opacity >= 0 && opacity <= 1;
  const isZindex = zIndex !== undefined && zIndex >= 0 && zIndex <= 9999;

  const dispatch = useDispatch();
  const onClick = () => {
    if (isSelectable) {
      dispatch(Actions.Draggable.action_onClickBead(bead.id));
    }
  };

  const translate3d = getTranslate3dCss(bead.position.x, bead.position.y);
  const rotate = bead.rotate ? `rotate(${bead.rotate}deg)` : "";
  let styles: React.CSSProperties = {
    transform: `${translate3d} ${rotate}`,
    opacity: isOpacity ? opacity : 1,
    zIndex: isZindex ? zIndex : undefined,
    border: isSelected ? "1px solid #000" : "",
    width: `${bead.width}px`,
    height: `${bead.height}px`,
    background: bead.color,
  };

  if (bead.type === BeadTypes.BasicEllipse) {
    styles.borderRadius = "100%";
  }

  return (
    <div className="bead-item" onClick={onClick} style={styles}>
      &nbsp;
    </div>
  );
};
