import React from "react";
import "rc-slider/assets/index.css";
import Slider, { createSliderWithTooltip } from "rc-slider";

export const SliderWithTooltip = createSliderWithTooltip(Slider);

export const FieldSliderWithLabel = ({ value, onChange, label, icon }) => {
  return (
    <div
      className={"fieldset"}
      style={{
        paddingTop: "10px",
        paddingBottom: "10px",
        paddingLeft: "10px",
        paddingRight: "10px",
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
        background: "#fff",
        borderBottom: 0,
      }}
    >
      <div className="label" style={{ padding: 0, width: "105px" }}>
        {icon ? (
          <div
            style={{
              width: "30px",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              height: "30px",
              marginRight: "10px",
            }}
          >
            {icon}
          </div>
        ) : null}
        {label ? <div>{label}</div> : null}
      </div>

      <div
        style={{
          boxSizing: "border-box",
          flexGrow: 1,
          paddingRight: "10px",
          paddingBottom: "10px",
        }}
      >
        <SliderWithTooltip
          max={100}
          min={1}
          step={1}
          value={value}
          onChange={value => onChange(value)}
          defaultValue={10}
          marks={{
            0: "0",
            50: "50",
            100: "100",
          }}
        />
      </div>
    </div>
  );
};
