import React from "react";

export const FieldCheckbox = ({ label, value, onChange, isDisabled }) => {
  return (
    <div className={"fieldset"}>
      <div className="label">{label}</div>
      <input
        type="checkbox"
        disabled={isDisabled}
        value={value === true ? 1 : 0}
        checked={value === true}
        onChange={event => onChange(event.target.value === "0")}
      />
    </div>
  );
};
