import React from "react";
import { uiButton } from "../../colors";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faDivide } from "@fortawesome/free-solid-svg-icons";
import { ToolbarFieldSet } from "./ToolbarFieldSet";
import { SliderWithTooltip } from "./FieldSlider";
import { FieldCheckbox } from "./FieldCheckbox";

export const FieldsetBeadSpacing = ({
  beadsAutoSpacing,
  beadsSpacing,
  onChange_beadsSpacing,
  onChange_beadsAutoSpacing,
}) => {
  const label =
    beadsAutoSpacing === true
      ? "auto"
      : beadsSpacing === undefined
      ? "0"
      : beadsSpacing;
  return (
    <ToolbarFieldSet
      selected={label}
      label={"Spacing"}
      icon={
        <FontAwesomeIcon
          icon={faDivide}
          color={uiButton}
          size={"lg"}
          style={{ transform: "rotate(90deg)" }}
        />
      }
    >
      <div>
        <div style={{ width: "100%" }}>
          <FieldCheckbox
            label={"Auto "}
            value={beadsAutoSpacing}
            onChange={onChange_beadsAutoSpacing}
            isDisabled={false}
          />
        </div>

        <div
          style={{
            width: "100%",
            paddingTop: "20px",
            paddingBottom: "30px",
            paddingLeft: "20px",
            paddingRight: "20px",
          }}
        >
          <SliderWithTooltip
            max={100}
            min={1}
            step={1}
            value={beadsSpacing}
            onChange={value => onChange_beadsSpacing(value)}
            defaultValue={10}
            marks={{
              0: "0",
              50: "50",
              100: "100",
            }}
          />
        </div>
      </div>
    </ToolbarFieldSet>
  );
};
