import React from "react";

export const ToolbarField = ({ label, icon, value, onToggle }) => {
  return (
    <div
      className={"fieldset"}
      onClick={onToggle}
      style={{ cursor: "pointer" }}
    >
      <div className="label">
        {icon ? (
          <div
            style={{
              width: "30px",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              height: "30px",
              marginRight: "10px",
            }}
          >
            {icon}
          </div>
        ) : null}
        {label ? <div>{label}</div> : null}
      </div>

      <div
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "flex-end",
          alignItems: "center",
          boxSizing: "border-box",
          paddingLeft: "10px",
          paddingRight: "20px",
          width: "100%",
        }}
      >
        <div>{value !== undefined ? value : "-"}</div>
      </div>
    </div>
  );
};
