import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { Editor } from "../../../editorObjects/Editor";
import { FieldBeadPreview } from "../ui/FieldBeadPreview";
import { Actions } from "../../../store";
import { FieldsetWidth } from "../ui/FieldsetWidth";
import { FieldsetHeight } from "../ui/FieldsetHeight";
import { FieldsetColor } from "../ui/FieldsetColor";
import { FieldsetBeadType } from "../ui/FieldsetBeadType";
import { FieldsetBeadSpacing } from "../ui/FieldsetBeadSpacing";
import { FieldsetBeadCount } from "../ui/FieldsetBeadCount";
import { FieldsetSize } from "../ui/FieldsetSize";
import { FieldsetBeadRotate } from "../ui/FieldsetBeadRotate";

export const ToolbarAddLine = ({ isVisible }) => {
  if (isVisible === true) {
    return <Content />;
  }

  return <React.Fragment />;
};

const Content = () => {
  return (
    <React.Fragment>
      <BeadPreview />
      <BeadSize />
      <BeadColor />
      <BeadType />
      <BeadsSpacing />
      <BeadsCount />
      <BeadsRotate />
    </React.Fragment>
  );
};

const BeadsRotate = () => {
  const dispatch = useDispatch();
  const settings = useSelector((state: Editor) => {
    return state.ui.lineSettings.settings;
  });
  if (!settings) {
    return <React.Fragment />;
  }

  if (!settings.defaultBead) {
    return <React.Fragment />;
  }

  const onChange_beadsRotate = value => {
    if (!value) {
      return;
    }
    dispatch(Actions.Toolbar.action_changeDefaultLineBeadRotate(Number(value)));
  };

  return (
    <FieldsetBeadRotate
      value={settings.defaultBead.rotate}
      onChange={onChange_beadsRotate}
    />
  );
};

const BeadSize = () => {
  const dispatch = useDispatch();
  const settings = useSelector((state: Editor) => {
    return state.ui.lineSettings.settings;
  });
  const onChange_beadWidth = value => {
    dispatch(Actions.Toolbar.action_changeDefaultLineBeadWidth(value));
  };
  const onChange_beadHeight = value => {
    dispatch(Actions.Toolbar.action_changeDefaultLineBeadHeight(value));
  };
  const onChange_keepAspectRatio = value => {
    dispatch(
      Actions.Toolbar.action_changeDefaultLineBeadKeepAspectRatio(value)
    );
  };
  if (!settings) {
    return <React.Fragment />;
  }
  if (!settings.defaultBead) {
    return <React.Fragment />;
  }
  const width = settings.defaultBead.width ? settings.defaultBead.width : 10;
  const height = settings.defaultBead.height ? settings.defaultBead.height : 10;
  const keepAspectRatio = settings.defaultBead.keepAspectRatio
    ? settings.defaultBead.keepAspectRatio
    : false;

  return (
    <div>
      <FieldsetSize
        height={height}
        width={width}
        keepAspectRatio={keepAspectRatio}
        onChange_beadHeight={onChange_beadHeight}
        onChange_beadWidth={onChange_beadWidth}
        onChange_keepAspectRatio={onChange_keepAspectRatio}
      />
    </div>
  );
};

const BeadsCount = () => {
  const dispatch = useDispatch();
  const settings = useSelector((state: Editor) => {
    return state.ui.lineSettings.settings;
  });

  if (!settings) {
    return <React.Fragment />;
  }

  const onChange_beadsCount = value => {
    if (!value) {
      return;
    }
    dispatch(Actions.Toolbar.action_changeDefaultLineBeadsCount(Number(value)));
  };

  const onChange_beadsAutoCount = value => {
    dispatch(Actions.Toolbar.action_changeDefaultLineBeadsAutoCount(value));
  };

  return (
    <FieldsetBeadCount
      beadsAutoCount={settings.beadsAutoCount}
      beadsCount={settings.beadsCount}
      onChange_beadsAutoCount={onChange_beadsAutoCount}
      onChange_beadsCount={onChange_beadsCount}
    />
  );
};

const BeadsSpacing = () => {
  const dispatch = useDispatch();
  const settings = useSelector((state: Editor) => {
    return state.ui.lineSettings.settings;
  });

  if (!settings) {
    return <React.Fragment />;
  }

  const onChange_beadsSpacing = value => {
    if (!value) {
      return;
    }
    dispatch(
      Actions.Toolbar.action_changeDefaultLineBeadsSpacing(Number(value))
    );
  };

  const onChange_beadsAutoSpacing = value => {
    dispatch(Actions.Toolbar.action_changeDefaultLineBeadsAutoSpacing(value));
  };

  return (
    <FieldsetBeadSpacing
      beadsAutoSpacing={settings.beadsAutoSpacing}
      beadsSpacing={settings.beadsSpacing}
      onChange_beadsAutoSpacing={onChange_beadsAutoSpacing}
      onChange_beadsSpacing={onChange_beadsSpacing}
    />
  );
};

const BeadType = () => {
  const dispatch = useDispatch();
  const settings = useSelector((state: Editor) => {
    return state.ui.lineSettings.settings;
  });

  if (!settings) {
    return <React.Fragment />;
  }
  if (!settings.defaultBead) {
    return <React.Fragment />;
  }
  if (!settings.defaultBead.type) {
    return <React.Fragment />;
  }

  const onSelect = type => {
    dispatch(Actions.Toolbar.action_changeDefaultLineBeadType(type));
  };

  return (
    <FieldsetBeadType onChange={onSelect} value={settings.defaultBead.type} />
  );
};

const BeadColor = () => {
  const dispatch = useDispatch();
  const settings = useSelector((state: Editor) => {
    return state.ui.lineSettings.settings;
  });

  if (!settings) {
    return <React.Fragment />;
  }
  if (!settings.defaultBead) {
    return <React.Fragment />;
  }
  if (!settings.defaultBead.color) {
    return <React.Fragment />;
  }

  const onSelect = color => {
    dispatch(Actions.Toolbar.action_changeDefaultLineColor(color));
  };

  return (
    <FieldsetColor onChange={onSelect} value={settings.defaultBead.color} />
  );
};

const BeadPreview = () => {
  const settings = useSelector((state: Editor) => {
    return state.ui.lineSettings.settings;
  });
  if (!settings) {
    return <React.Fragment />;
  }
  if (!settings.defaultBead) {
    return <React.Fragment />;
  }
  return <FieldBeadPreview bead={settings.defaultBead} />;
};

