import React, { useState } from "react";
import { ToolbarField } from "./ToolbarField";

export const ToolbarFieldSet = ({ selected, label, icon, children }) => {
  const [isPickerOpen, setIsPickerOpen] = useState(false);
  const onToggle = () => {
    setIsPickerOpen(!isPickerOpen);
  };
  return (
    <div
      style={{
        // borderBottom: "1px solid #dfdfdf",
        borderTop: "1px solid #dfdfdf",
      }}
    >
      <ToolbarField
        label={label}
        onToggle={onToggle}
        icon={icon}
        value={selected}
      />

      {isPickerOpen === true ? (
        <div style={{ background: "#fff" }}>{React.cloneElement(children)}</div>
      ) : null}
    </div>
  );
};
