import React, { useEffect, useState } from "react";
import { Editor, EditorMode } from "../editorObjects/Editor";
import { EditorModes } from "./Toolbar/EditorModes";
import { AddLineMode } from "./Line/AddLineMode";
import { EditorChangeState } from "./Toolbar/EditorChangeState";
import { useSelector, useStore, useDispatch } from "react-redux";
import { MouseSelectionBeads } from "./Bead/MouseSelectionBeads";
import { MouseSelectionLines } from "./Line/MouseSelectionLines";
import { DisplayBeads } from "./Bead/DisplayBeads";
import { DisplayLines } from "./Line/DisplayLines";
import { GroupSelectionPlaceholder } from "./operations/GroupSelectionPlaceholder";
import { PickBeadColor } from "./Toolbar/PickColor";
import { ChangeLinePosition } from "./Toolbar/ChangeLinePosition";
import { PickBeadType } from "./Toolbar/PickBeadType";
import { ChangeBeadRotate } from "./Toolbar/ChangeBeadRotate";
import { LineInspector } from "./Toolbar/LineInspector";
import { Actions } from "../store";
import { EditToolbar } from "./Toolbar/EditToolbar";
import { ToolbarEditLines } from "./Toolbar/Modes/ToolbarEditLines";
import { ToolbarEditBeads } from "./Toolbar/Modes/ToolbarEditBeads";
import { ToolbarAddLine } from "./Toolbar/Modes/ToolbarAddLine";
import { ToolbarIdle } from "./Toolbar/Modes/ToolbarIdle";

export const EditorScene = () => {
  const editorMode = useSelector((state: Editor) => {
    return state.mode;
  });
  const store = useStore();
  const state = store.getState();
  console.log("EditorRerender: ", state);

  // @TODO: Dodac funkcje ktora zostaje uruchomiona po rozpoczeciu aplikajci
  const dispatch = useDispatch();
  const [firstPass, setFirstPass] = useState(false);
  useEffect(() => {
    if (process.env.NODE_ENV && process.env.NODE_ENV === "development") {
      if (firstPass === false) {
        dispatch(Actions.Persist.action_readSavedCanvas());
        dispatch(
          Actions.ChangeEditorMode.action_onChangeEditorMode(
            EditorMode.EditLines
          )
        );
        dispatch(Actions.Draggable.action_onClickLastestLine());
        setFirstPass(true);
      }
    }
  });

  return (
    <div className="scene">
      <div className="topbar-wrapper">
        <EditorChangeState />
      </div>

      <div className="actions-wrapper">
        <EditorModes editorMode={editorMode} />
      </div>

      <div className={"scene-wrapper"}>
        <AddLineMode isActive={editorMode === EditorMode.AddLine} />

        <MouseSelectionLines isVisible={editorMode === EditorMode.EditLines} />

        <MouseSelectionBeads isVisible={editorMode === EditorMode.EditBeads} />

        <DisplayBeads mode={editorMode} />

        <DisplayLines mode={editorMode} />

        <GroupSelectionPlaceholder mode={editorMode} />
      </div>

      <div className={"toolbar-wrapper"}>
        <ToolbarEditLines isVisible={editorMode === EditorMode.EditLines} />
        <ToolbarEditBeads isVisible={editorMode === EditorMode.EditBeads} />
        <ToolbarAddLine isVisible={editorMode === EditorMode.AddLine} />
        <ToolbarIdle isVisible={editorMode === EditorMode.Idle} />

        {/* <ChangeLinePosition mode={editorMode} />

        <PickBeadType mode={editorMode} />
        <ChangeBeadRotate mode={editorMode} />
        <PickBeadColor mode={editorMode} />
        <LineInspector mode={editorMode} />

        <EditToolbar mode={editorMode} /> */}
      </div>
    </div>
  );
};
