import React from "react";
import { uiButton } from "../../colors";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPalette } from "@fortawesome/free-solid-svg-icons";
import { ToolbarFieldSet } from "./ToolbarFieldSet";
import { FieldColorPicker } from "./FieldColorPicker";
import { FieldColor } from "./FieldColor";

export const FieldsetColor = ({ value, onChange }) => {
  return (
    <ToolbarFieldSet
      selected={
        <FieldColor value={value} isSelected={false} onClick={undefined} />
      }
      label={"Color"}
      icon={<FontAwesomeIcon icon={faPalette} color={uiButton} size={"lg"} />}
    >
      <FieldColorPicker value={value} onChange={onChange} />
    </ToolbarFieldSet>
  );
};
