import { immerable } from "immer";
import { Line, Point } from "./Editor";
import { generate } from "shortid";
import { normalize, scale, add, buildPoint } from "./Point";
import { merge } from "lodash";
import { DeepPartial } from "ts-essentials";
import { buildBead } from "./Bead";

export const buildLine = (props: DeepPartial<Line>): Line => {
  const __defaults = {
    id: generate(),
    start: buildPoint(10, 10),
    end: buildPoint(100, 100),
    beadIds: [],
    settings: {
      beadsCount: 0,
      beadsAutoCount: true,
      beadsSpacing: 0,
      beadsAutoSpacing: false,
      defaultBead: buildBead({}),
    },
    [immerable]: true,
  };

  return merge(__defaults, props);
};

export const getLength = (line: Line): number => {
  return Math.sqrt(
    (line.end.x - line.start.x) * (line.end.x - line.start.x) +
      (line.end.y - line.start.y) * (line.end.y - line.start.y)
  );
};

export const getPointFromStart = (line: Line, distance: number): Point => {
  const x = line.end.x - line.start.x;
  const y = line.end.y - line.start.y;
  let point = buildPoint(x, y);
  point = normalize(point);
  point = scale(point, distance);
  point = add(point, line.start.x, line.start.y);
  return point;
};

export const getPointFromStartLine = (
  startX: number,
  startY: number,
  endX: number,
  endY: number,
  distance: number
): Point => {
  const x = endX - startX;
  const y = endY - startY;
  let point = buildPoint(x, y);
  point = normalize(point);
  point = scale(point, distance);
  point = add(point, startX, startY);
  return point;
};
