import { BeadTypes } from "../editorObjects/Editor";
import { BeadSnapperImmerReducer } from "./BeadSnapperImmerReducer";
import { buildBead } from "../editorObjects/Bead";
import { buildLine } from "../editorObjects/Line";
import { merge } from "lodash";

export class ToolbarImmerReducer extends BeadSnapperImmerReducer {
  action_changeBeadFill(color: string) {
    Object.keys(this.draftState.ui.selectedBeadsIds).forEach(beadId => {
      this.draftState.appData.beads[beadId].color = color;
    });
  }

  action_changeBeadType(type: BeadTypes) {
    Object.keys(this.draftState.ui.selectedBeadsIds).forEach(beadId => {
      this.draftState.appData.beads[beadId].type = type;
    });
  }
  action_onBeadRotate(beadId, { deg }) {}
  action_onBeadsRotate({ deg }) {}

  action_changeSelectedLinesBeadsSpacing(value) {
    Object.keys(this.draftState.ui.selectedLinesIds).forEach(id => {
      this.draftState.appData.lines[id].settings.beadsSpacing = value;
      this.updateBeadsPositions(id);
    });
  }

  action_changeSelectedLinesBeadsAutoSpacing(value) {
    Object.keys(this.draftState.ui.selectedLinesIds).forEach(id => {
      this.draftState.appData.lines[id].settings.beadsAutoSpacing = value;
      this.updateBeadsPositions(id);
    });
  }

  action_changeSelectedLinesBeadsCount(value) {
    Object.keys(this.draftState.ui.selectedLinesIds).forEach(id => {
      this.draftState.appData.lines[id].settings.beadsCount = value;
      this.updateBeadsPositions(id);
    });
  }

  action_changeSelectedLinesBeadsAutoCount(value) {
    Object.keys(this.draftState.ui.selectedLinesIds).forEach(id => {
      this.draftState.appData.lines[id].settings.beadsAutoCount = value;
      this.updateBeadsPositions(id);
    });
  }

  action_resetSelectedLines() {
    const firstLineId = Object.keys(this.draftState.ui.selectedLinesIds)[0];
    const firstLine = this.draftState.appData.lines[firstLineId];
    const defaultBead = { ...firstLine.settings.defaultBead };

    defaultBead.id = undefined;
    defaultBead.position = undefined;

    Object.keys(this.draftState.ui.selectedLinesIds).forEach(id => {
      this.draftState.appData.lines[id].beadIds.forEach(beadId => {
        if (defaultBead) {
          this.draftState.appData.beads[beadId] = merge(
            this.draftState.appData.beads[beadId],
            defaultBead
          );
        }
      });
      this.draftState.appData.lines[id].settings.defaultBead = merge(
        this.draftState.appData.lines[id].settings.defaultBead,
        defaultBead
      );
      this.updateBeadsPositions(id);
    });
  }

  action_deleteSelectedLines() {
    Object.keys(this.draftState.ui.selectedLinesIds).forEach(id => {
      this.draftState.appData.lines[id].beadIds.forEach(beadId => {
        delete this.draftState.appData.beads[beadId];
      });
      delete this.draftState.appData.lines[id];
    });
    this.draftState.ui.selectedLinesIds = {};
    this.draftState.ui.selectedLinesIdsCount = 0;
  }

  action_cloneSelectedLines() {
    const moveX = 20;
    const moveY = 20;
    const newLinesIds: string[] = [];

    Object.keys(this.draftState.ui.selectedLinesIds).forEach(id => {
      const newLine = buildLine({
        ...this.draftState.appData.lines[id],
        id: undefined,
        beadIds: [],
      });
      newLine.start.x = newLine.start.x + moveX;
      newLine.start.y = newLine.start.y + moveY;
      newLine.end.x = newLine.end.x + moveX;
      newLine.end.y = newLine.end.y + moveY;

      this.draftState.appData.lines[id].beadIds.forEach(beadId => {
        const newBead = buildBead({
          ...this.draftState.appData.beads[beadId],
          id: undefined,
        });
        newBead.position.x = newBead.position.x + moveX;
        newBead.position.y = newBead.position.y + moveY;
        this.draftState.appData.beads[newBead.id] = newBead;
        newLine.beadIds.push(newBead.id);
      });

      newLinesIds.push(newLine.id);
      this.draftState.appData.lines[newLine.id] = newLine;
    });

    newLinesIds.forEach(lineId => {
      this.updateBeadsPositions(lineId);
    });

    this.draftState.ui.selectedLinesIds = {};
    this.draftState.ui.selectedLinesIdsCount = 0;

    newLinesIds.forEach(lineId => {
      this.draftState.ui.selectedLinesIds[lineId] = true;
      this.draftState.ui.selectedLinesIdsCount =
        this.draftState.ui.selectedLinesIdsCount + 1;
    });
  }

  /**
   *
   *
   *
   *
   *
   *
   *
   *
   *
   *
   *
   *
   *
   *
   */

  action_changeDefaultLineBeadsCount(value) {
    if (this.draftState.ui.lineSettings.settings) {
      this.draftState.ui.lineSettings.settings.beadsCount = value;
    }
  }

  action_changeDefaultLineBeadsAutoCount(value) {
    if (this.draftState.ui.lineSettings.settings) {
      this.draftState.ui.lineSettings.settings.beadsAutoCount = value;
    }
  }

  action_changeDefaultLineBeadsSpacing(value) {
    if (this.draftState.ui.lineSettings.settings) {
      this.draftState.ui.lineSettings.settings.beadsSpacing = value;
    }
  }

  action_changeDefaultLineBeadsAutoSpacing(value) {
    if (this.draftState.ui.lineSettings.settings) {
      this.draftState.ui.lineSettings.settings.beadsAutoSpacing = value;
    }
  }

  action_changeDefaultLineBeadType(value) {
    if (this.draftState.ui.lineSettings.settings) {
      if (this.draftState.ui.lineSettings.settings.defaultBead) {
        this.draftState.ui.lineSettings.settings.defaultBead.type = value;
      }
    }
  }

  action_changeDefaultLineColor(value) {
    if (this.draftState.ui.lineSettings.settings) {
      if (this.draftState.ui.lineSettings.settings.defaultBead) {
        this.draftState.ui.lineSettings.settings.defaultBead.color = value;
      }
    }
  }

  action_changeDefaultLineBeadHeight(value) {
    const { lineSettings } = this.draftState.ui;
    if (lineSettings.settings) {
      if (lineSettings.settings.defaultBead) {
        const {
          width,
          height,
          keepAspectRatio,
        } = lineSettings.settings.defaultBead;
        if (keepAspectRatio && width && height) {
          if (height >= 1) {
            const ratio = width / height;
            lineSettings.settings.defaultBead.width = value * ratio;
            lineSettings.settings.defaultBead.height = value;
          } else {
            lineSettings.settings.defaultBead.height = 1;
          }
        } else {
          lineSettings.settings.defaultBead.height = value;
        }
      }
    }
  }

  action_changeDefaultLineBeadWidth(value) {
    const { lineSettings } = this.draftState.ui;
    if (lineSettings.settings) {
      if (lineSettings.settings.defaultBead) {
        const {
          width,
          height,
          keepAspectRatio,
        } = lineSettings.settings.defaultBead;
        if (keepAspectRatio && width && height) {
          if (width >= 1) {
            const ratio = height / width;
            lineSettings.settings.defaultBead.width = value;
            lineSettings.settings.defaultBead.height = value * ratio;
          } else {
            lineSettings.settings.defaultBead.width = 1;
          }
        } else {
          lineSettings.settings.defaultBead.width = value;
        }
      }
    }
  }

  action_changeDefaultLineBeadKeepAspectRatio(value) {
    const { lineSettings } = this.draftState.ui;
    if (lineSettings.settings) {
      if (lineSettings.settings.defaultBead) {
        lineSettings.settings.defaultBead.keepAspectRatio = value;
      }
    }
  }
  action_changeDefaultLineBeadRotate(value) {
    const { lineSettings } = this.draftState.ui;
    if (lineSettings.settings) {
      if (lineSettings.settings.defaultBead) {
        lineSettings.settings.defaultBead.rotate = value;
      }
    }
  }

  /**
   *
   *
   *
   *
   *
   *
   *
   *
   *
   *
   *
   *
   *
   *
   */

  action_changeSelectedLines_settings_beadsCount(value) {
    Object.keys(this.draftState.ui.selectedLinesIds).forEach(id => {
      this.draftState.appData.lines[id].settings.beadsCount = value;
      this.updateBeadsPositions(id);
    });
  }

  action_changeSelectedLines_settings_beadsAutoCount(value) {
    Object.keys(this.draftState.ui.selectedLinesIds).forEach(id => {
      this.draftState.appData.lines[id].settings.beadsAutoCount = value;
      this.updateBeadsPositions(id);
    });
  }

  action_changeSelectedLines_settings_beadsSpacing(value) {
    Object.keys(this.draftState.ui.selectedLinesIds).forEach(id => {
      this.draftState.appData.lines[id].settings.beadsSpacing = value;
      this.updateBeadsPositions(id);
    });
  }

  action_changeSelectedLines_settings_beadsAutoSpacing(value) {
    Object.keys(this.draftState.ui.selectedLinesIds).forEach(id => {
      this.draftState.appData.lines[id].settings.beadsAutoSpacing = value;
      this.updateBeadsPositions(id);
    });
  }

  action_changeSelectedLines_defaultBead_type(value) {
    Object.keys(this.draftState.ui.selectedLinesIds).forEach(lineId => {
      this.draftState.appData.lines[lineId].settings.defaultBead.type = value;
      this.draftState.appData.lines[lineId].beadIds.forEach(beadId => {
        this.draftState.appData.beads[beadId].type = value;
      });
      this.updateBeadsPositions(lineId);
    });
  }

  action_changeSelectedLines_defaultBead_color(value) {
    Object.keys(this.draftState.ui.selectedLinesIds).forEach(lineId => {
      this.draftState.appData.lines[lineId].settings.defaultBead.color = value;
      this.draftState.appData.lines[lineId].beadIds.forEach(beadId => {
        this.draftState.appData.beads[beadId].color = value;
      });
      this.updateBeadsPositions(lineId);
    });
  }

  action_changeSelectedLines_defaultBead_height(value) {
    Object.keys(this.draftState.ui.selectedLinesIds).forEach(lineId => {
      const currLine = this.draftState.appData.lines[lineId];
      let { width, height, keepAspectRatio } = currLine.settings.defaultBead;

      if (keepAspectRatio === false) {
        currLine.settings.defaultBead.height = value;
        this.draftState.appData.lines[lineId].beadIds.forEach(beadId => {
          this.draftState.appData.beads[beadId].height = value;
        });
        this.updateBeadsPositions(lineId);
      } else {
        if (width && height) {
          if (value >= 1) {
            const ratio = width / height;
            width = value * ratio;
            height = value;
          }

          currLine.settings.defaultBead.width = width;
          currLine.settings.defaultBead.height = height;
        }

        this.draftState.appData.lines[lineId].beadIds.forEach(beadId => {
          if (width && height) {
            this.draftState.appData.beads[beadId].width = width;
            this.draftState.appData.beads[beadId].height = height;
          }
        });

        this.updateBeadsPositions(lineId);
      }
    });
  }

  action_changeSelectedLines_defaultBead_width(value) {
    Object.keys(this.draftState.ui.selectedLinesIds).forEach(lineId => {
      const currLine = this.draftState.appData.lines[lineId];
      let { width, height, keepAspectRatio } = currLine.settings.defaultBead;

      if (keepAspectRatio === false) {
        currLine.settings.defaultBead.width = value;
        this.draftState.appData.lines[lineId].beadIds.forEach(beadId => {
          this.draftState.appData.beads[beadId].width = value;
        });
        this.updateBeadsPositions(lineId);
      } else {
        if (width && height) {
          if (value >= 1) {
            const ratio = height / width;
            width = value;
            height = value * ratio;
          }

          currLine.settings.defaultBead.width = width;
          currLine.settings.defaultBead.height = height;
        }

        this.draftState.appData.lines[lineId].beadIds.forEach(beadId => {
          if (width && height) {
            this.draftState.appData.beads[beadId].width = width;
            this.draftState.appData.beads[beadId].height = height;
          }
        });

        this.updateBeadsPositions(lineId);
      }
    });
  }

  action_changeSelectedLines_defaultBead_keepAspectRatio(value) {
    Object.keys(this.draftState.ui.selectedLinesIds).forEach(lineId => {
      this.draftState.appData.lines[
        lineId
      ].settings.defaultBead.keepAspectRatio = value;
      this.draftState.appData.lines[lineId].beadIds.forEach(beadId => {
        this.draftState.appData.beads[beadId].keepAspectRatio = value;
      });
      this.updateBeadsPositions(lineId);
    });
  }

  action_changeSelectedLines_defaultBead_rotate(value) {
    Object.keys(this.draftState.ui.selectedLinesIds).forEach(lineId => {
      this.draftState.appData.lines[lineId].settings.defaultBead.rotate = value;
      this.draftState.appData.lines[lineId].beadIds.forEach(beadId => {
        this.draftState.appData.beads[beadId].rotate = value;
      });
      this.updateBeadsPositions(lineId);
    });
  }

  /****
   *
   *
   *
   *
   *
   *
   *
   *
   */

  action_changeSelectedBeads_type(value) {
    Object.keys(this.draftState.ui.selectedBeadsIds).forEach(beadId => {
      this.draftState.appData.beads[beadId].type = value;
    });
    this.updateLineFromSelectedBeads();
  }

  action_changeSelectedBeads_color(value) {
    Object.keys(this.draftState.ui.selectedBeadsIds).forEach(beadId => {
      this.draftState.appData.beads[beadId].color = value;
    });
    this.updateLineFromSelectedBeads();
  }

  action_changeSelectedBeads_height(value) {
    Object.keys(this.draftState.ui.selectedBeadsIds).forEach(beadId => {
      const currBead = this.draftState.appData.beads[beadId];
      let { width, height, keepAspectRatio } = currBead;

      if (keepAspectRatio === false) {
        this.draftState.appData.beads[beadId].height = value;
      } else {
        if (width && height) {
          if (value >= 1) {
            const ratio = width / height;
            width = value * ratio;
            height = value;
          }
        }
        this.draftState.appData.beads[beadId].width = width;
        this.draftState.appData.beads[beadId].height = height;
      }
    });

    this.updateLineFromSelectedBeads();
  }

  action_changeSelectedBeads_width(value) {
    Object.keys(this.draftState.ui.selectedBeadsIds).forEach(beadId => {
      const currBead = this.draftState.appData.beads[beadId];
      let { width, height, keepAspectRatio } = currBead;

      if (keepAspectRatio === false) {
        this.draftState.appData.beads[beadId].width = value;
      } else {
        if (width && height) {
          if (value >= 1) {
            const ratio = height / width;
            width = value;
            height = value * ratio;
          }
        }
        this.draftState.appData.beads[beadId].width = width;
        this.draftState.appData.beads[beadId].height = height;
      }
    });

    this.updateLineFromSelectedBeads();
  }

  action_changeSelectedBeads_keepAspectRatio(value) {
    Object.keys(this.draftState.ui.selectedBeadsIds).forEach(beadId => {
      this.draftState.appData.beads[beadId].keepAspectRatio = value;
    });
    this.updateLineFromSelectedBeads();
  }

  action_changeSelectedBeads_rotate(value) {
    Object.keys(this.draftState.ui.selectedBeadsIds).forEach(beadId => {
      this.draftState.appData.beads[beadId].rotate = value;
    });
    this.updateLineFromSelectedBeads();
  }

  updateLineFromSelectedBeads() {
    // @TODO
  }
}
