import React from "react";
import { uiButton } from "../../colors";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faDiceSix } from "@fortawesome/free-solid-svg-icons";
import { ToolbarFieldSet } from "./ToolbarFieldSet";
import { SliderWithTooltip } from "./FieldSlider";
import { FieldCheckbox } from "./FieldCheckbox";

export const FieldsetBeadCount = ({
  beadsAutoCount,
  beadsCount,
  onChange_beadsCount,
  onChange_beadsAutoCount,
}) => {
  const label =
    beadsAutoCount === true
      ? "auto"
      : beadsCount === undefined
      ? "0"
      : beadsCount;

  const max = 300;
  return (
    <ToolbarFieldSet
      selected={label}
      label={"Amount"}
      icon={<FontAwesomeIcon icon={faDiceSix} color={uiButton} size={"lg"} />}
    >
      <div>
        <div style={{ width: "100%" }}>
          <FieldCheckbox
            label={"Auto "}
            value={beadsAutoCount}
            onChange={onChange_beadsAutoCount}
            isDisabled={false}
          />
        </div>

        <div
          style={{
            width: "100%",
            paddingTop: "20px",
            paddingBottom: "30px",
            paddingLeft: "20px",
            paddingRight: "20px",
          }}
        >
          <SliderWithTooltip
            max={max}
            min={1}
            step={1}
            value={beadsCount}
            onChange={value => onChange_beadsCount(value)}
            defaultValue={10}
            marks={{
              1: "1",
              [max / 2]: `${max / 2}`,
              [max]: `${max}`,
            }}
          />
        </div>
      </div>
    </ToolbarFieldSet>
  );
};
