import React from "react";
import { FieldsetHeight } from "./FieldsetHeight";
import { FieldsetWidth } from "./FieldsetWidth";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faExpandArrowsAlt,
  faArrowsAltV,
  faArrowsAltH,
  faLink,
  faUnlink,
} from "@fortawesome/free-solid-svg-icons";
import { uiButton } from "../../colors";
import { ToolbarFieldSet } from "./ToolbarFieldSet";
import { FieldSliderWithLabel } from "./FieldSliderWithLabel";
import { FieldCheckbox } from "./FieldCheckbox";

export const FieldsetSize = ({
  height,
  width,
  keepAspectRatio,
  onChange_beadHeight,
  onChange_beadWidth,
  onChange_keepAspectRatio,
}) => {
  const format = value =>
    value.toLocaleString(undefined, {
      maximumFractionDigits: 2,
      style: "decimal",
    });
  const value = `${format(width)} x ${format(height)}`;
  return (
    <div>
      <ToolbarFieldSet
        selected={value}
        label={"Size"}
        icon={
          <FontAwesomeIcon
            icon={faExpandArrowsAlt}
            color={uiButton}
            size={"lg"}
          />
        }
      >
        <div>
          <div style={{ width: "100%", background: "#fff" }}>
            <FieldCheckbox
              label={
                <div
                  style={{
                    display: "flex",
                    flexWrap: "nowrap",
                    justifyContent: "space",
                  }}
                >
                  <div
                    style={{
                      width: "30px",
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                      height: "30px",
                      marginRight: "10px",
                      background: "#fff",
                    }}
                  >
                    <FontAwesomeIcon
                      icon={keepAspectRatio === true ? faLink : faUnlink}
                      color={uiButton}
                      size={"lg"}
                    />
                  </div>
                  <div>Keep aspect ratio</div>
                </div>
              }
              value={keepAspectRatio}
              onChange={onChange_keepAspectRatio}
              isDisabled={false}
            />
          </div>

          <FieldSliderWithLabel
            label={"Width"}
            icon={
              <FontAwesomeIcon
                icon={faArrowsAltH}
                color={uiButton}
                size={"lg"}
              />
            }
            value={width}
            onChange={onChange_beadWidth}
          />

          <FieldSliderWithLabel
            label={"Height"}
            icon={
              <FontAwesomeIcon
                icon={faArrowsAltV}
                color={uiButton}
                size={"lg"}
              />
            }
            value={height}
            onChange={onChange_beadHeight}
          />
        </div>
      </ToolbarFieldSet>
    </div>
  );
};
