import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { Editor } from "../../../editorObjects/Editor";
import { FieldBeadPreview } from "../ui/FieldBeadPreview";
import { Actions } from "../../../store";
import { FieldsetWidth } from "../ui/FieldsetWidth";
import { FieldsetHeight } from "../ui/FieldsetHeight";
import { FieldsetColor } from "../ui/FieldsetColor";
import { FieldsetBeadType } from "../ui/FieldsetBeadType";
import { FieldsetBeadSpacing } from "../ui/FieldsetBeadSpacing";
import { FieldsetBeadCount } from "../ui/FieldsetBeadCount";
import { FieldsetSize } from "../ui/FieldsetSize";
import { FieldDeleteAndCopy } from "../ui/FieldDeleteAndCopy";
import { FieldsetBeadRotate } from "../ui/FieldsetBeadRotate";

export const ToolbarEditLines = ({ isVisible }) => {
  if (isVisible === true) {
    return <ToolbarEditLinesWrapper />;
  }

  return <React.Fragment />;
};

const ToolbarEditLinesWrapper = ({}) => {
  const showToolbar = useSelector((state: Editor) => {
    return state.ui.selectedLinesIdsCount >= 1;
  });
  if (showToolbar) {
    return <Content />;
  }
  return <React.Fragment />;
};

const Content = () => {
  return (
    <React.Fragment>
      <BeadPreview />
      <BeadSize />
      <BeadColor />
      <BeadType />
      <BeadsSpacing />
      <BeadsCount />
      <BeadsRotate />
      <DeleteAndClone />
    </React.Fragment>
  );
};

const BeadsRotate = () => {
  const dispatch = useDispatch();
  const selected = useSelector((state: Editor) => {
    const lineId = Object.keys(state.ui.selectedLinesIds)[0];
    return state.appData.lines[lineId];
  });
  if (!selected) {
    return <React.Fragment />;
  }
  const { settings } = selected;

  if (!settings) {
    return <React.Fragment />;
  }

  if (!settings.defaultBead) {
    return <React.Fragment />;
  }

  const onChange_beadsRotate = value => {
    if (!value) {
      return;
    }
    dispatch(
      Actions.Toolbar.action_changeSelectedLines_defaultBead_rotate(
        Number(value)
      )
    );
  };

  return (
    <FieldsetBeadRotate
      value={settings.defaultBead.rotate}
      onChange={onChange_beadsRotate}
    />
  );
};

const BeadSize = () => {
  const dispatch = useDispatch();
  const selected = useSelector((state: Editor) => {
    const lineId = Object.keys(state.ui.selectedLinesIds)[0];
    return state.appData.lines[lineId];
  });
  if (!selected) {
    return <React.Fragment />;
  }
  const { settings } = selected;

  const onChange_beadWidth = value => {
    dispatch(
      Actions.Toolbar.action_changeSelectedLines_defaultBead_width(value)
    );
  };
  const onChange_beadHeight = value => {
    dispatch(
      Actions.Toolbar.action_changeSelectedLines_defaultBead_height(value)
    );
  };
  const onChange_keepAspectRatio = value => {
    dispatch(
      Actions.Toolbar.action_changeSelectedLines_defaultBead_keepAspectRatio(
        value
      )
    );
  };
  if (!settings) {
    return <React.Fragment />;
  }
  if (!settings.defaultBead) {
    return <React.Fragment />;
  }
  const width = settings.defaultBead.width ? settings.defaultBead.width : 10;
  const height = settings.defaultBead.height ? settings.defaultBead.height : 10;
  const keepAspectRatio = settings.defaultBead.keepAspectRatio
    ? settings.defaultBead.keepAspectRatio
    : false;

  return (
    <div>
      <FieldsetSize
        height={height}
        width={width}
        keepAspectRatio={keepAspectRatio}
        onChange_beadHeight={onChange_beadHeight}
        onChange_beadWidth={onChange_beadWidth}
        onChange_keepAspectRatio={onChange_keepAspectRatio}
      />
    </div>
  );
};

