const BeadType = () => {
  const dispatch = useDispatch();
  const selected = useSelector((state: Editor) => {
    const lineId = Object.keys(state.ui.selectedLinesIds)[0];
    return state.appData.lines[lineId];
  });
  if (!selected) {
    return <React.Fragment />;
  }
  const { settings } = selected;

  if (!settings) {
    return <React.Fragment />;
  }
  if (!settings.defaultBead) {
    return <React.Fragment />;
  }
  if (!settings.defaultBead.type) {
    return <React.Fragment />;
  }

  const onSelect = type => {
    dispatch(Actions.Toolbar.action_changeSelectedLines_defaultBead_type(type));
  };

  return (
    <FieldsetBeadType onChange={onSelect} value={settings.defaultBead.type} />
  );
};

