const BeadPreview = () => {
  const selected = useSelector((state: Editor) => {
    const lineId = Object.keys(state.ui.selectedLinesIds)[0];
    return state.appData.lines[lineId];
  });
  if (!selected) {
    return <React.Fragment />;
  }
  const { settings } = selected;

  if (!settings) {
    return <React.Fragment />;
  }
  if (!settings.defaultBead) {
    return <React.Fragment />;
  }
  return <FieldBeadPreview bead={settings.defaultBead} />;
};

const DeleteAndClone = () => {
  const dispatch = useDispatch();
  const onDeleteLines = () => {
    dispatch(Actions.Toolbar.action_deleteSelectedLines());
  };
  const onCloneLines = () => {
    dispatch(Actions.Toolbar.action_cloneSelectedLines());
  };
  const onResetBeads = () => {
    dispatch(Actions.Toolbar.action_resetSelectedLines());
  };

  return (
    <React.Fragment>
      <FieldDeleteAndCopy
        onDelete={onDeleteLines}
        onClone={onCloneLines}
        onReset={onResetBeads}
      />
    </React.Fragment>
  );
};
