import React from "react";
import "rc-slider/assets/index.css";
import Slider, { createSliderWithTooltip } from "rc-slider";

export const SliderWithTooltip = createSliderWithTooltip(Slider);

export const FieldSlider = ({ value, onChange, ...props }) => {
  return (
    <div
      style={{
        background: "#fff",
        width: "100%",
        paddingTop: "20px",
        paddingBottom: "30px",
        paddingLeft: "20px",
        paddingRight: "20px",
      }}
    >
      <SliderWithTooltip
        max={100}
        min={1}
        step={1}
        value={value}
        onChange={value => onChange(value)}
        defaultValue={10}
        marks={{
          0: "0",
          50: "50",
          100: "100",
        }}
        {...props}
      />
    </div>
  );
};
