import React from "react";
import { BeadTypes } from "../../../editorObjects/Editor";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircle, faExpand } from "@fortawesome/free-solid-svg-icons";

export const BeadTypesIcons = {
  [BeadTypes.BasicEllipse]: "circle",
  [BeadTypes.BasicRectangle]: "rectangle-wide",
};

export const FieldBeadTypePicker = ({ value, onChange }) => {
  return (
    <DisplayAvailableBeadTypes
      types={BeadTypesIcons}
      selectedType={value}
      onClick={onChange}
    />
  );
};

const DisplayAvailableBeadTypes = ({ types, selectedType, onClick }) => {
  console.log("jak jest?", types);
  return (
    <div className="fieldset" style={{}}>
      <div className="label"></div>
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "flex-start",
          alignItems: "center",
          boxSizing: "border-box",
          paddingLeft: "10px",
          paddingRight: "20px",
          width: "100%",
        }}
      >
        {Object.keys(types).map((type, index) => {
          return (
            <DisplayBeadType
              icon={types[type]}
              type={type}
              key={index}
              isSelected={selectedType === type}
              onClick={onClick}
            />
          );
        })}
      </div>
    </div>
  );
};

export const DisplayBeadType = ({ icon, type, isSelected, onClick }) => {
  return (
    <div style={{ border: "5px solid transparent" }}>
      <div
        onClick={onClick ? () => onClick(type) : undefined}
        style={{
          width: 25,
          height: 25,
          background: "#eee",
          marginBottom: 0,
          marginRight: 0,
          border: isSelected ? "2px solid #000" : "2px solid #000",
          boxShadow: isSelected
            ? "2px 2px 2px rgba(0,0,0,1)"
            : "2px 2px 2px rgba(0,0,0,0.1)",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        {icon === "circle" ? <FontAwesomeIcon icon={faCircle} /> : null}
        {icon === "rectangle-wide" ? <FontAwesomeIcon icon={faExpand} /> : null}
      </div>
    </div>
  );
};
