import React from "react";
import { uiButton } from "../../colors";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faShapes } from "@fortawesome/free-solid-svg-icons";
import { ToolbarFieldSet } from "./ToolbarFieldSet";
import {
  FieldBeadTypePicker,
  BeadTypesIcons,
  DisplayBeadType,
} from "./FieldBeadTypePicker";

export const FieldsetBeadType = ({ value, onChange }) => {
  return (
    <ToolbarFieldSet
      selected={
        <DisplayBeadType
          icon={value ? BeadTypesIcons[value] : undefined}
          type={value}
          isSelected={false}
          onClick={undefined}
        />
      }
      label={"Type"}
      icon={<FontAwesomeIcon icon={faShapes} color={uiButton} size={"lg"} />}
    >
      <FieldBeadTypePicker value={value} onChange={onChange} />
    </ToolbarFieldSet>
  );
};
