import React from "react";
import { useSelector } from "react-redux";
import { Editor } from "../../../editorObjects/Editor";
import { ToolbarField } from "../ui/ToolbarField";

export const ToolbarIdle = ({ isVisible }) => {
  if (isVisible === true) {
    return <Content />;
  }

  return <React.Fragment />;
};

const Content = () => {
  const linesLen = useSelector((state: Editor) => {
    return Object.keys(state.appData.lines).length;
  });

  const beadsLen = useSelector((state: Editor) => {
    return Object.keys(state.appData.beads).length;
  });

  return (
    <div>
      <ToolbarField
        label={"Beads"}
        onToggle={undefined}
        value={beadsLen}
        icon={undefined}
      />

      <ToolbarField
        label={"Lines"}
        onToggle={undefined}
        value={linesLen}
        icon={undefined}
      />
    </div>
  );
};
